.about-section {
    display: flex;
    margin-bottom: 5vh;
    margin-top: 2vh;
}
.about-section img {
    margin-left: 2vh;
    margin-right: 2vh;
    margin-top: auto;
    margin-bottom: auto;
    height: 100%;
    max-width: height;
}
@media screen and (max-width: 600px) {
    .about-section {
        display: inline;
        align-items: center;
    }
  }